import {cloneElement, useState} from "react";
import Slider from "react-slick";
import Portal from "./Portal";
import {XSymbol} from "../img"

const PhotoModal = ({trigger, src, idx = 0}) => {
    const [open, setOpen] = useState(false)
    return (
        <>
            {trigger && cloneElement(trigger, {onClick: _ => setOpen(true)})}
            {
                open ?
                    <Portal elementId="modal-root">
                        <div className="popup-bg">
                            <div className="close" onClick={_ => setOpen(false)}>
                                <XSymbol fill='#fff'/>
                            </div>
                            <div className="popup bg-white w-full relative rounded-xl">
                                <Slider dots infinite speed={500} initialSlide={idx}>
                                    {src.map(s => <img key={s} src={s} alt=""/>)}
                                </Slider>
                            </div>
                        </div>
                    </Portal>
                    : null
            }
        </>
    );
}

export default PhotoModal;