import {cloneElement, useState} from "react";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import Portal from "./Portal";

const AccountModal = ({trigger, title, accounts}) => {

    const [open, setOpen] = useState(false)

    return (
        <>
            {trigger && cloneElement(trigger, {onClick: _ => setOpen(true)})}
            {
                open ?
                    <Portal elementId="modal-root">
                        <div className="popup-bg">
                            <div className="popup bg-white rounded-xl shadow-lg p-6 mx-auto w-9/12">
                                <div>
                                    <div className="mb-[30px] text-center">
                                        <div className="text-lg text-slate-500">{title}</div>
                                    </div>

                                    {
                                        accounts.map(account => (
                                            <div key={account.title} className="mb-[40px]">
                                                <div
                                                    className="pb-[10px] mb-[10px] border-b border-dashed border-rose-200">
                                                    <span className="font-bold">{account.title}</span>

                                                    <CopyToClipboard text={`${account.bank} ${account.account}`}>
                                                        <button className="copy shadow focus:outline-none"></button>
                                                    </CopyToClipboard>
                                                </div>

                                                <p>{`${account.bank} (: ${account.holder})`}</p>
                                                <p>{account.account}</p>
                                            </div>
                                        ))
                                    }
                                </div>

                                <button className="close shadow focus:outline-none" onClick={_ => setOpen(false)}>
                                </button>
                            </div>
                        </div>
                    </Portal>
                    : null
            }
        </>
    );
}

export default AccountModal;